import Typed from 'typed.js';
import { TypeAnimation } from 'react-type-animation';
import myphoto from '../Components/assets/myphoto.png'
import { Box, Button, Text } from '@chakra-ui/react';
const TopSection =()=>{




  return (
 <Box className="wrapper"  mt={{base:"65px",sm:"65px",md:"60px",xl:"2px"}}>
    <div className="cols cols0">
<Text mt={{base:"50px"}} className="topline">Hii there!</Text>
<div className='words'>

<h3 className="iam">I am Uzair a<span className="multitext">
  <br />
<TypeAnimation
sequence={[
  'Coder', // Types 'One'
  1000, // Waits 1s
  'Software Engineer', // Deletes 'One' and types 'Two'
  2000, // Waits 2s
  'Web Developer',
  1000 // Types 'Three' without deleting 'Two'
  //   () => {
    //     console.log('Done typing!'); // Place optional callbacks anywhere in the array
    //   }
  ]}
  wrapper="span"
  cursor={true}
  repeat={Infinity}
  style={{ fontSize: '0.8em' }}
/></span></h3>
  </div>
<Text style={{color:"white",fontSize:"25px",lineHeight:"40px"}}>A Passionate Full Stack Web Developer</Text>
 <Box as={"flex"} gap={"20px"}>
    <Button style={{marginRight:"30px",backgroundColor:"white",marginTop:"30px",color:"black",border:"1px solid black"}}>Download Resume</Button>
    <Button style={{marginRight:"30px",backgroundColor:"white",marginTop:"30px",color:"black",border:"1px solid black"}}>My Projects</Button>
   </Box>
   
    </div>

<div className="cols cols1"></div>


<div className="imagebox">
    {/* <img width={"200px"} src="https://previews.123rf.com/images/nottomanv1/nottomanv11610/nottomanv1161000390/65646095-abstract-purple-watercolor-on-white-background-this-is-watercolor-splash-it-is-drawn-by-hand-.jpg" id="splash" alt="" /> */}
    <img src={myphoto}  alt="" />
</div>

</Box>
)}
  
export default TopSection