import { ReactNode } from 'react';
import {Link as RLink} from 'react-router-dom'
import {
  Box,
  Flex,
  Avatar,
  Link,
  Button,
  Menu,
  MenuButton,
  MenuList,
  MenuItem,
  MenuDivider,
  useDisclosure,
  useColorModeValue,
  Stack,
  useColorMode,
  Text,
  Center,
} from "@chakra-ui/react";
import { MoonIcon, SunIcon } from '@chakra-ui/icons';
// import myresum from './assets/Uzair_Resume.pdf'
const NavLink = ({ children }: { children: ReactNode }) => (
  <Link
    px={2}
    py={1}
    rounded={'md'}
    _hover={{
      textDecoration: 'none',
      bg: useColorModeValue('gray.200', 'gray.700'),
    }}
    href={'#'}>
    {children}
  </Link>
);

export default function Navbarfun() {
  const { colorMode, toggleColorMode } = useColorMode();
  const { isOpen, onOpen, onClose } = useDisclosure();
  return (
    <>
    <div className='navbar' id="nav-menu">

      <Box bg={useColorModeValue('gray.100', 'gray.900')} px={4}>
        <Flex h={16} alignItems={'center'} justifyContent={'space-between'}>
          <Box width={{base:"200px",sm:"240px",md:"300px",xl:"350px"}}><span className='logo'>FOLIO</span> <b className='dot'>.</b></Box>
<Box className='navItems' display={{base:"none",sm:"none",md:"flex"}} gap={"50px"} style={{border:"2px solid red",width:"75%", fontSize:"20px"}} cursor={"pointer"}>
  <RLink to="/">
    <Text className={"nav-link home"}>Home</Text>
    
    </RLink>
  <RLink to="/skills">
    <Text  className="nav-link skills">Skills</Text>
    </RLink>
  <RLink to="/projects">
    <Text className="nav-link projects">Projects</Text>
    </RLink>
  <RLink to="/about">
    <Text className="nav-link about">About</Text>
    </RLink>
  <RLink to="/contact">
    <Text className="nav-link contact">Contact</Text>
    </RLink>  
  <RLink to="/contact">
    <Text className="nav-link resume">Resume</Text>
    </RLink>  
</Box>
          <Flex alignItems={'center'}>
            <Stack direction={'row'} spacing={7}>
              <Button onClick={toggleColorMode}>
                {colorMode === 'light' ? <MoonIcon /> : <SunIcon />}
              </Button>

              <Menu>
                <MenuButton
                  as={Button}
                  rounded={'full'}
                  variant={'link'}
                  cursor={'pointer'}
                  minW={0}>
                  <Avatar
                    size={'sm'}
                    src={'https://avatars.dicebear.com/api/male/username.svg'}
                  />
                </MenuButton>
                <MenuList alignItems={'center'}>
                  <br />
                  <Center>
                    <Avatar
                      size={'2xl'}
                      src={'https://avatars.dicebear.com/api/male/username.svg'}
                      />
                  </Center>
                  <br />
                  <Center>
                    <p>Username</p>
                  </Center>
                  <br />
                  <MenuDivider />
                  <MenuItem>Your Servers</MenuItem>
                  <MenuItem>Account Settings</MenuItem>
                  <MenuItem>Logout</MenuItem>
                </MenuList>
              </Menu>
            </Stack>
          </Flex>
        </Flex>
      </Box>
                      </div>
    </>
  );
}